<template>
    <div class='root'>
        <action-bar title='预售活动详情'></action-bar>
        <div class='container'>
            <scroll-view class='content' :data="data">
                <div class="sc">
                    <!-- 活动数据 -->
                <div class="fillet-bg">
                    <div class="item-title">活动数据</div>
                    <div class="flex-row-center margin-top">
                        <div class="flex-row-child data-color">¥{{ data.totalAmount }}</div>
                        <div class="flex-row-child data-color">{{ data.orderCount }}单</div>
                        <div class="flex-row-child data-color">{{ data.productCount }}扎</div>
                    </div>
                    <div class="flex-row-center margin-top">
                        <div class="flex-row-child">成交总额</div>
                        <div class="flex-row-child">成交订单</div>
                        <div class="flex-row-child">成交商品量</div>
                    </div>
                    <div class="flex-row-center margin-top">
                        <div class="flex-row-child data-color">{{ data.violateCount }}单</div>
                        <div class="flex-row-child data-color">¥{{ data.violateAmount }}</div>
                        <div class="flex-row-child data-color"></div>
                    </div>
                    <div class="flex-row-center margin-top margin-bottom">
                        <div class="flex-row-child">违约订单量</div>
                        <div class="flex-row-child">违约金额</div>
                        <div class="flex-row-child"></div>
                    </div>
                </div>
                <!-- 活动内容 -->
                <div class="fillet-bg">
                    <div class="item-title">活动内容<text v-if="data.priceModel == '浮动模式'"
                            class="item-content-tips">（尾款结算时间:您输入尾款金额 至 发货前一天）</text></div>
                    <div class="item-content-title">标题： {{ data.title }}</div>
                    <div class="flex-row-center margin-bottom">
                        <div class="item-content-num">品种数量： {{ data.skuCount }}</div>
                        <div>类型：{{ data.priceModel }}</div>
                    </div>
                    <div class="margin-bottom">
                        <span>活动时间：</span><span class="item-content-time">{{ data.startSalesTime }} 至
                            {{ data.endSalseTime }}</span>
                    </div>
                    <div>
                        <span>预计送达时间：</span><span class="item-content-time">{{ data.startDeliveryDate }} 至
                            {{ data.endDeliveryDate }}</span>
                    </div>
                </div>
                <!-- 商品列表 -->
                <div class="fillet-bg">
                    <div class="flex-row-center justify-between margin-bottom">
                        <div class="item-title">列表商品</div>
                        <div>{{ num }}件</div>
                    </div>
                    <div v-for="item in data.productList" :key="item.id">
                        <goods-item>
                            <van-image slot="imgSlot" radius=".25rem" :src="baseUrl + item.imageUrl" class="goods-img" />
                            <div slot="nameSlot">{{ item.productName }}</div>
                            <div slot="attributeSlot">
                                <div class="attribute">
                                    <div class="attribute-content">库存：{{ item.inventory }}</div>
                                    <div class="attribute-content">销量：{{ item.salesCount }}</div>
                                </div>
                                <div v-if="data.priceModel =='固定模式'" class="attribute">
                                    <div class="attribute-content">售价：{{ item.earnest }}</div>
                                </div>
                                <div v-else class="attribute">
                                    <div class="attribute-content">定金：{{ item.earnest }}</div>
                                    <div class="attribute-content">结算售价：{{ item.unitPrice }}</div>
                                </div>
                            </div>
                        </goods-item>
                    </div>
                </div>
                </div>
            </scroll-view>
        </div>
    </div>
</template>
<script>

import { presellActivityInfo } from '@/http/api'
import goodsItem from '@/components/util/GoodsItem.vue'

export default {
  name: 'reserveDetail',
  components: {
    goodsItem
  },
  data () {
    return {
      data: {}, // 详情数据
      num: 0 // 商品总数量，非品种数量
    }
  },
  computed: {
    id () {
      return this.$route.query.id
    },
    baseUrl () {
      return this.$store.state.baseUrl
    }
  },
  methods: {
    // 获取详情数据
    getReserveDetail () {
      presellActivityInfo({
        id: this.id
      }).then(res => {
        let num = 0
        res.data.productList.forEach(elt => {
          num += elt.inventory
        })
        this.num = num
        this.data = res.data
      }).catch(res => {
        this.$toast(res.msg)
      })
    }
  },
  mounted () {
    this.getReserveDetail()
  }
}
</script>
<style lang="scss" scoped>
.root {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #f2f2f2;
}

.container {
    flex-grow: 1;
    position: relative;
}

.content {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.sc{
    padding-top:  .5rem;
    padding-bottom: .0031rem;
}

.fillet-bg {
    padding:  .5rem;
    margin: .5rem;
    margin-top: 0;
    background-color: white;
    border-radius: .375rem;
    font-size: .875rem;
}

.item-title {
    font-size: 1rem;
}

.flex-row-center {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.justify-between{
  justify-content: space-between;
}

.flex-row-child {
    width: 33%;
    text-align: center;
}

.data-color {
    color: #999999;
}

.margin-top {
    margin-top: .8125rem;
}

.margin-bottom {
    margin-bottom: .8125rem;
}

.item-content-tips {
    color: #999999;
    font-size: .75rem;
}

.item-content-title {
    font-size: 30rpx;
    color: #42D18A;
    margin: .8125rem 0;
}

.item-content-num {
    margin-right: 1.875rem;
}

.item-content-time {
    color: red;
}

.goods-img {
    width: 20vw;
    height: 20vw;
    background-color: #F2F2F2;
    margin-right: .5rem;
}

.attribute {
    display: flex;
    flex-direction: row;
    font-size: .75rem;
}

.attribute-content {
    width: 50%;
}

</style>
